import React, { Component } from 'react'
import classNames from 'classnames'
import ComponentList from 'components/Common/ComponentList'
import Icon from 'components/Common/Icon'

export default class HistoryCard extends Component {
  state = {
    active: false,
    height: 0,
    isPlaying: false,
    position: 0
  }

  _cardBody = React.createRef()

  controlPlaying = () => {
    if (!this.state.active) return this.openCard()
    return this.setState({ isPlaying: !this.state.isPlaying })
  }

  openCard = () => {
    const { clientHeight } = this._cardBody.current.firstChild
    return this.setState({ active: true, height: clientHeight, isPlaying: true })
  }

  render = () => (
    <div className={classNames('PV8 HistoryCard Pointer', {
        Active: this.state.active,
      })}>
      <div
        className="Card BGWhite"
        onClick={this.controlPlaying}>
        <CardHead
          title={this.props.title}
          isPlaying={this.state.isPlaying} />
        <div
          className="CardBodyWrapper"
          ref={this._cardBody}
          style={{ height: this.state.height }}>
          <CardBody playlist={this.props.playlist} position={this.state.position} />
        </div>
      </div>
    </div>
  )
}

const CardHead = ({ title, play, isPlaying }) => (
  <div className="CardHead Flex JCSpaceBetween">
    <div className="FS4x">
      <div className="ColorGray Saira SairaLight MB8">
        Conheça a história
      </div>
      <div className="ColorPrimary Saira SairaStrong Uppercase">
        {title}
      </div>
    </div>
    <img src={`/static/img/ic_playlist_${!isPlaying ? 'play' : 'pause'}_24px.svg`} />
  </div>
)

const CardBody = ({ playlist = [], position }) => (
  <div className="CardBody">
    <ComponentList component={Sound} componentProps={{ position }}>
      {playlist}
    </ComponentList>
  </div>
)

const Sound = ({ title, src, time, index, position }) => {
  const isSelected = index === position
  const play = {
    active: '/static/img/ic_play_circle_filled_24px.svg',
    disable: '/static/img/ic_play_circle_outline_24px.svg',
  }
  const icon = isSelected ? play.active : play.disable
  return (
    <div className={classNames(
        'Flex AICenter PV4 JCSpaceBetween Saira TextLeft',
        { ColorGray: !isSelected, ColorPrimary: isSelected }
    )}>
      <div className="Image" style={{ backgroundImage: `url(${icon})` }} />
      <div className="W100 PH16">{title}</div>
      <div>{time}</div>
        <audio id="audio" preload="auto">
          <source src={src} type="audio/mp3" />
        </audio>
    </div>
  )
}
